body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: #ccffcc;
}

.Header{
  font-family: 'Courgette', cursive;
  margin-left: 70px;
  font-size: 20px;
  color:#cccc55;
  text-shadow: 4px 4px 5px #222200;
}
#headingStripe{
  width:890px;
  height:50px;
  background: #55cc55;
  position: relative;
  right:30px;
  bottom:45px;
  z-index: -1;
  text-align: center;
  line-height: 60px;
  font-style: italic;
  color:#ffff55;
  text-shadow: 2px 2px 7px black;
}

#appContainer{
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  padding-bottom: 100px;
  background: #55cc55;
  margin:auto;
}

#DisplayStatus{
  text-align: center;
}
#DisplayStatus h3{
  display: inline-block;
  margin: 10px;
}

.Booker{
  display:inline-block;
  margin: 20px;
  text-align: center;
  font-family: 'Krub', sans-serif;
  color:#eef;
  text-shadow: 1px 1px 5px #559;
}
.Booker h2{
  position: relative;
  top:15px;
}

.Checkmark{
  height:20px;
  width:20px;
  margin-left: 4px;
}

.optionImage{
  width:200px;
  height:240px;
}

.bookButton{
  float:left;
  background: #9f9;
  font-weight:bold;
  color:#222;
  padding:6px;
  border:none;
  border-radius:3px;
}
.bookButton:hover{
  background: #5f5;
  box-shadow: 2px 2px green;
  cursor:pointer;
}
.unbookButton{
  float:right;
  background: #f99;
  font-weight:bold;
  color:#222;
  padding:6px;
  border:none;
  border-radius:3px;
}
.unbookButton:hover{
  background: #f55;
  box-shadow: 2px 2px red;
  cursor:pointer;
}

.leftButton{
  position: relative;
  bottom:150px;
  right:90px;
  height:70px;
  font-size: 20px;
  background: #afa;
  border: none;
}
.leftButton:hover{
  background-color: #3f3;
  cursor:pointer;
}
.rightButton{
  position: relative;
  bottom:150px;
  left:75px;
  height:70px;
  font-size: 20px;
  background: #afa;
  border: none;
}
.rightButton:hover{
  background-color: #3f3;
  cursor:pointer;
}

@media only screen and (max-width:768px){
  .Header{
    font-size: 14px;
    bottom:20px;
    line-height: 50px;
    margin-left: 40px;
  }
  #headingStripe{
    width:105%;
  }
}

